import * as cheerio from "cheerio";

export async function onRequestGet({ request }) {
  const { searchParams } = new URL(request.url);
  const page = searchParams.get("p") || 0;
  const targetURL = `https://topdesix.com/page/${page}/`;

  try {
    const headers = {
      Accept:
        "text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.7",
      "Accept-Language": "en-US,en;q=0.9",
      "User-Agent":
        "Mozilla/5.0 (iPhone; CPU iPhone OS 16_6 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/16.6 Mobile/15E148 Safari/604.1",
      Cookie: "_ym_uid=1731821805711588892; _ym_d=1731821805; is_18=true",
    };

    const response = await fetch(targetURL, { headers });

    if (!response.ok) {
      return new Response(
        JSON.stringify({ error: "Failed to fetch the page" }),
        {
          status: response.status,
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
          },
        }
      );
    }

    const html = await response.text();
    const videos = extractVideoData(html);

    return new Response(JSON.stringify(videos), {
      status: 200,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    });
  } catch (error) {
    return new Response(JSON.stringify({ error: error.message }), {
      status: 500,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    });
  }
}

function extractVideoData(html) {
  const cheerio = require("cheerio"); // Ensure Cheerio is loaded
  const $ = cheerio.load(html);

  const videoElements = $(".video-block"); // Select video block container
  return videoElements
    .map((_, video) => {
      const element = $(video);
      const title = element.find(".infos .title").text().trim(); // Extract the video title
      const time = element.find(".video-datas .views-number").first().text().trim(); // Extract the video duration
      const preview = element.find(".thumb-img").attr("src"); // Extract the preview image URL
      const href = element.find(".thumb").attr("href"); // Extract the video link
      const videoUrl = element.find(".thumb-video").attr("src"); // Extract the video URL (trailer)

      return { title, time, preview, href, videoUrl };
    })
    .get(); // Convert the result to an array
}
