import * as cheerio from "cheerio";

export async function onRequestGet({ request }) {
  const { searchParams } = new URL(request.url);
  const videoUrl = searchParams.get("videoUrl");

  if (!videoUrl) {
    return new Response(
      JSON.stringify({ error: "Missing videoUrl parameter" }),
      {
        status: 400,
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      }
    );
  }

  try {
    const headers = {
      Accept:
        "text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.7",
      "Accept-Language": "en-US,en;q=0.9",
      "User-Agent":
        "Mozilla/5.0 (iPhone; CPU iPhone OS 16_6 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/16.6 Mobile/15E148 Safari/604.1",
    };

    const response = await fetch(videoUrl, { headers });

    if (!response.ok) {
      return new Response(
        JSON.stringify({ error: "Failed to fetch the page" }),
        {
          status: response.status,
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
          },
        }
      );
    }

    const html = await response.text();
    const videoData = extractVideoData(html);

    return new Response(JSON.stringify(videoData, html), {
      status: 200,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    });
  } catch (error) {
    return new Response(JSON.stringify({ error: error.message }), {
      status: 500,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    });
  }
}

function extractVideoData(html) {
    const cheerio = require("cheerio"); // Ensure Cheerio is imported
    const $ = cheerio.load(html);
  
    const sources = [];
    // Select all <source> tags inside the <video> tag
    $("video#censor-player source").each((_, element) => {
      const src = $(element).attr("src"); // Extract the src attribute
      let label = $(element).attr("label"); // Extract the label attribute
      if (label) {
        label = label.replace(/<[^>]+>/g, '').trim(); // Clean HTML tags
      } else {
        label = "Unknown"; // Default if no label
      }
      if (src) {
        sources.push({
          quality: label, // Cleaned quality
          url: src, // Add the MP4 URL
        });
      }
    });
  
    // Return the extracted sources or an error if none found
    if (sources.length > 0) {
      return { sources };
    } else {
      return { error: "No MP4 links found", html: html };
    }
  }
  
  
