export async function onRequestGet({ request }) {
  const { searchParams } = new URL(request.url);
  const videoUrl = searchParams.get("videoUrl");

  if (!videoUrl) {
    return new Response(
      JSON.stringify({ error: "Missing videoUrl parameter" }),
      {
        status: 400,
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      }
    );
  }

  // Convert the URL to the embed version
  const embedUrl = convertToEmbedUrl(videoUrl);

  if (!embedUrl) {
    return new Response(
      JSON.stringify({ error: "Invalid video URL format" }),
      {
        status: 400,
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      }
    );
  }

  // Return the embed link as JSON
  return new Response(
    JSON.stringify({ embedUrl }),
    {
      status: 200,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    }
  );
}

function convertToEmbedUrl(url) {
  try {
    const urlParts = new URL(url);
    const pathSegments = urlParts.pathname.split("/");

    if (pathSegments.length >= 3) {
      return `${urlParts.origin}/embed/video/${pathSegments[2]}`;
    }

    return null;
  } catch {
    return null;
  }
}
