import * as cheerio from "cheerio";

export async function onRequestGet({ request }) {
  const { searchParams } = new URL(request.url);
  const page = searchParams.get("p") || 0;
  const targetURL = `https://erothots1.com/videos/new?p=${page}/`;

  try {
    const headers = {
      "Accept": "text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.7",
      "Accept-Encoding": "gzip, deflate, br",
      "Accept-Language": "en-US,en;q=0.9",
      "Cache-Control": "no-cache",
      "Referer": "https://erothots1.com/videos/new", // Ensure this matches the browsing path
      "Origin": "https://erothots1.com",
      "User-Agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/130.0.0.0 Safari/537.36", // Match your browser
    };
    

    const response = await fetch(targetURL, { headers });

    if (!response.ok) {
      return new Response(
        JSON.stringify({ error: "Failed to fetch the page" }),
        {
          status: response.status,
          headers: {
            "Content-Type": "application/json",
            "Access-Control-Allow-Origin": "*",
          },
        }
      );
    }

    const html = await response.text();
    const { videos, hasNextPage } = extractVideoData(html);

    return new Response(
      JSON.stringify({ videos, nextPage: hasNextPage }),
      {
        status: 200,
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
      }
    );
  } catch (error) {
    return new Response(JSON.stringify({ error: error.message }), {
      status: 500,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    });
  }
}

function extractVideoData(html) {
  const cheerio = require("cheerio"); // Ensure cheerio is loaded
  const $ = cheerio.load(html);

  // Select video containers with either .video or .video.video-media classes
  const videoElements = $("a.video, a.video.video-media");

  const videos = videoElements
    .map((_, video) => {
      const element = $(video);
      const title = element.find("h3.title").text().trim(); // Extract the video title
      const time = element.find(".thumb .caption").text().trim(); // Extract the video duration

      // Extract the preview image URL, prioritizing `data-src` over `src`
      const preview =
        element.find(".thumb img.lazy").attr("data-src") ||
        element.find(".thumb img.lazy").attr("src");

      const href = element.attr("href"); // Extract the video link
      const videoUrl = element.find(".thumb .player").attr("data-video"); // Extract the video URL (if available)

      return { title, time, preview, href, videoUrl };
    })
    .get(); // Convert the result to an array

  // Check if "Load More" element exists to determine if there's a next page
  const hasNextPage = !!$('#loadmore').length;

  return { videos, hasNextPage };
}
